<template>
    <div class="organizeDetails">
<!--      -->
      <div class="nav">
        <div class="navBox typeArea">
          <div class="breadCrumb">
            <el-breadcrumb separator="/">
              <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
              <el-breadcrumb-item><router-link to="/organize.html"></router-link></el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
          </div>
        </div>
      </div>
<!--     -->
      <div class="content">
        <div class="contentBox typeArea">
          <div class="message">
            <div class="msgImg">
              <img :src="details.orgImg | getOneImg" alt="">
            </div>
            <div class="msgText">
              <p><span>{{details.orgName || ''}}</span></p>
              <p><span>{{details.totalPeople || ''}}</span></p>
              <p><span>{{details.totalMember || ''}}</span></p>
              <p>    <span>{{details.userName || ''}}</span></p>
              <p><span>{{details.mobile || ''}}</span></p>
              <p></p>
            </div>
          </div>

          <div class="organizeImg">
            <img :src="details.orgDeptImg | getOneImg" alt="">
          </div>

          <div class="introduce">
            <h2 class="introduceTitle">/</h2>
            <p class="introduceText">
              {{details.orgInfo || ''}}
            </p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "organizeDetails",
        data(){
          return{
            id:this.Base64.decode(this.$route.query.id),
            details:{}
          }
        },
        methods:{
          getMsg(){
            this.$axios.post('/fun/deptOrg/info',this.qs({
              orgId:this.id
            })).then(msg=>{
              if(msg.status == 200){
                console.log(msg);
                this.details = msg.data.data;
              }else{
                this.$message.error('');
              }
            }).catch(err=>{
              this.$message.error('');
            })
          }
        },
      created(){
        this.getMsg()
      }

    }
</script>

